import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { Observable, of } from 'rxjs';
import { User } from '../models/user.model';

@Injectable({
    providedIn: 'root'
})
export class UserService {
    private db_childPath = '/user';

    userRef: AngularFireList<User>;

    constructor(private db: AngularFireDatabase)
    {
        this.userRef = db.list(this.db_childPath);
    }

    // 取得列表中所有資料
    getAll(): AngularFireList<User>
    {
        return this.userRef;
    }

    getSingleUser(key: string): AngularFireList<User>
    {
        return this.db.list(this.db_childPath + "/" + key);
    }

    // 新增一列資料
    create(news: User): any
    {
        return this.userRef.push(news);
    }

    // 更新資料
    /*
     * @params key->id
     * @params value->update data
     * Promise = 非同步操作
     */
    update(key: string, value: any): Promise<void>
    {
        return this.userRef.update(key, value);
    }

    // 刪除一列資料
    delete(key: string): Promise<void>
    {
        return this.userRef.remove(key);
    }

    // 刪除所有資料(刪除不帶任何參數)
    deleteAll(): Promise<void>
    {
        return this.userRef.remove();
    }
}
