import { Component, OnInit } from '@angular/core';
import { NewsService } from 'src/app/services/news.service';
import { News } from 'src/app/models/news.model';
import { map } from 'rxjs/operators';

// 靜態資源
import { path } from 'src/environments/path.map';

// 測試資料
// import { news } from '../fake.news';

@Component({
    selector: 'app-news-view',
    templateUrl: './news-view.component.html',
    styleUrls: ['./news-view.component.css']
})

export class NewsViewComponent implements OnInit {
    newsData?: News[];
    currentNews?: News;
    currentIndex = -1;
    title = '';

    // news = news;

    constructor(private newsService: NewsService) { }

    // Console.log textcolor & fontsize
    // https://www.telerik.com/blogs/how-to-style-console-log-contents-in-chrome-devtools
    ngOnInit(): void 
    {
        // console.exception('test');
        this.retrieveNews();
    }

    refreshList(): void
    {
        this.currentNews = undefined;
        this.currentIndex = -1;
        this.retrieveNews();
    }

    retrieveNews(): void
    {
        this.newsService.getAll()
            .snapshotChanges()
            .pipe(
                map(changes => 
                    changes.map(c =>
                        ({ key: c.payload.key, ...c.payload.val() })
                    )
                )
            ).subscribe(data => {
                // splice the Array
                this.newsData = data;
            });
    }

    setActiveNews(news: News, index: number): void
    {
        this.currentNews = news;
        this.currentIndex = index;
    }

    removeAllNews(): void
    {
        this.newsService.deleteAll()
            .then( () => this.refreshList() )
            .catch(err => console.log(err));
    }
}
