import { Injectable } from '@angular/core';
import { AngularFireDatabase, AngularFireList } from '@angular/fire/database';
import { News } from '../models/news.model';

@Injectable({
    providedIn: 'root'
})

export class NewsService {
    private db_childPath = '/news';

    newsRef: AngularFireList<News>;

    constructor(private db: AngularFireDatabase)
    {
        this.newsRef = db.list(this.db_childPath);
    }

    // 取得列表中所有資料
    getAll(): AngularFireList<News>
    {
        return this.newsRef;
    }

    // 新增一列資料
    create(news: News): any
    {
        return this.newsRef.push(news);
    }

    // 更新資料
    /*
     * @params key->id
     * @params value->update data
     * Promise = 非同步操作
     */
    update(key: string, value: any): Promise<void>
    {
        return this.newsRef.update(key, value);
    }

    // 刪除一列資料
    delete(key: string): Promise<void>
    {
        return this.newsRef.remove(key);
    }

    // 刪除所有資料(刪除不帶任何參數)
    deleteAll(): Promise<void>
    {
        return this.newsRef.remove();
    }
}
