import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AppComponent } from './app.component';
import { HomeComponent } from './home/home.component';
import { UsersViewComponent } from './component-users/users-view/users-view.component';
import { NotFoundComponent } from './not-found/not-found.component';
import { NewsAddComponent } from './component-news/news-add/news-add.component';
import { AuthGuard } from './auth/auth.guard';

// 靜態資源
import { path } from 'src/environments/path.map';

// Guard
import { EditingGuard } from './auth/editing.guard';
import { CanLoadArticleGuard } from './auth/can-load-article.guard';
import { LogoutComponent } from './component-login/logout/logout.component';

const routes: Routes = [
  {
    path: path.home,
    component: HomeComponent,
    // canDeactivate: [EditingGuard]
  },
  {
    path: 'add',
    component: NewsAddComponent
  },
  {
    path: path.users,
    canActivate: [AuthGuard],
    loadChildren: () => import('./component-users/component-users.module').then(m => m.ComponentUsersModule)
  },
  {
    path: path.rooms,
    loadChildren: () => import('./component-rooms/component-rooms.module').then(m => m.ComponentRoomsModule)
  },
  {
    path: path.chats,
    loadChildren: () => import('./component-chats/component-chats.module').then(m => m.ComponentChatsModule)
  },
  {
    path: path.article,
    // canLoad: [CanLoadArticleGuard],
    loadChildren: () => import('./component-articles/component-articles.module').then(m => m.ComponentArticlesModule)
  },
  {
    path: path.login,
    loadChildren: () => import('./component-login/component-login.module').then(m => m.ComponentLoginModule)
  },
  {
    path: path.logout,
    component: LogoutComponent,
    // 需要建立路徑守門員
  },
  {
    path: '**',
    component: NotFoundComponent,
    pathMatch: 'full'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {onSameUrlNavigation: 'reload', enableTracing: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
